import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import Menus from '@/components/Menus'
import titlebar from "@/components/titlebar";

Vue.use(VueRouter)

const routes = [
  {
    //메인 홈
    path: '/',
    name: 'Home',
    components: {
      title:titlebar,
      menu: Menus,
      content: Home,
    },
    props: true,
  },
  {
    //로그인
    path: '/Login',
    name: 'Login',
    component: () => import('@/views/Login')
  },
  {
    //회원가입
    path: '/signup',
    name: 'Signup',
    component: () => import('@/components/signup')
  },
  {
    //회원가입 완료창
    path: '/signup_complete',
    name: 'SignupComplete',
    component: () => import('@/components/signup_complete')
  },
  //여기는 메뉴



]

const router = new VueRouter({
  routes,
  mode: 'history',
  base: '/',
})

export default router
