<template>
  <div>
    <v-navigation-drawer
        id="app-drawer"
        v-model="drawer"
        app
        dark
        floating
        persistent
        width="280"
    >
      <div>
        <v-layout
            class="fill-height"
            tag="v-list"
            column
        >
          <v-list>
            <v-list-item @click="movePage('');">
              <v-toolbar-title><v-icon class="mr-2">민석강</v-icon>대충메뉴</v-toolbar-title>
            </v-list-item>
            <hr class="mt-2 mb-2">
          </v-list>
        </v-layout>
      </div>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  components: {
  },
  data: () => ({
    drawer: null,
    color: 'success',
    responsive: false,
  }),
  mounted(){
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
  },
  methods: {
    movePage(target){
      this.$router.push({ name: target });
    },
    onResponsiveInverted () {
      if (window.innerWidth < 1000) {
        this.responsive = true
      } else {
        this.responsive = false
      }
    },
  },
}
</script>

<style lang="scss">
#app-drawer {
  .v-list__tile {
    border-radius: 4px;
    &--buy {
      margin-top: auto;
      margin-bottom: 17px;
    }
  }
}
</style>